<script>
  import { onMount } from "svelte";

  let today = new Date();

  $: hour = today.getHours();
  $: min = today.getMinutes();
  $: sec = today.getSeconds();

  onMount(() => {
    const interval = setInterval(() => {
      today = new Date();
    }, 1000);
    return () => {
      clearInterval(interval);
    };
  });
</script>

<style>
  @import "../static/global.css";
</style>

<h1>{hour}<span>:</span>{min}<span>:</span>{sec}</h1>
